import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'

import userText from './views/user/userText.vue'
import userList from './views/user/userList.vue'

import rounterChartList from './views/rounterChart/rounterChartList.vue'

import questionnaire1  from './views/questionNaire/questionnaire-1.vue'
import questionnaire2  from './views/questionNaire/questionnaire-2.vue'
import questionnaire3  from './views/questionNaire/questionnaire-3.vue'
import questionnaire4  from './views/questionNaire/questionnaire-4.vue'

import message1  from './views/message/message-1.vue'
import message2  from './views/message/message-2.vue'
import message3  from './views/message/message-3.vue'


let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '导航一',
    //     iconCls: 'el-icon-message',//图标样式class
    //     children: [
    //         { path: '/main', component: Main, name: '主页', hidden: true },
    //         { path: '/table', component: Table, name: 'Table' },
    //         { path: '/form', component: Form, name: 'Form' },
    //     ]
    // },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '导航二',
    //     iconCls: 'fa fa-id-card-o',
    //     children: [
    //         { path: '/page4', component: Page4, name: '页面4' },
    //         { path: '/page5', component: Page5, name: '页面5' }
    //     ]
    // },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//只有一个节点
    //     children: [
    //         { path: '/page6', component: Page6, name: '导航三' }
    //     ]
    // },
    // {
    //     path: '/',
    //     component: Home,
    //     name: 'Charts',
    //     iconCls: 'fa fa-bar-chart',
    //     children: [
    //         { path: '/echarts', component: echarts, name: 'echarts' }
    //     ]
    // },
    {
        path: '/',
        component: Home,
        name: '用户管理',
        iconCls: 'el-icon-user',
        children: [
            // { path: '/userText', component: userText, name: '用户列表' },
            { path: '/userList', component: userList, name: '用户列表' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '问卷管理',
        iconCls: 'el-icon-tickets',
        children: [
                    { path: '/questionNaire-1', component: questionnaire1, name: '问卷草稿箱' },
                    { path: '/questionNaire-2', component: questionnaire2, name: '已发布问卷' },
                    { path: '/questionNaire-3', component: questionnaire3, name: '已截止问卷' },
                    { path: '/questionNaire-4', component: questionnaire4, name: '作废问卷' }
                ]
    },
    {
        path: '/',
        component: Home,
        name: '消息管理',
        iconCls: 'el-icon-bell',
        children: [
            { path: '/message-1', component: message1, name: '系统消息' },
            { path: '/message-2', component: message2, name: '学院消息' },
            { path: '/message-3', component: message3, name: '个人消息' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-c-scale-to-original',
        leaf: true,//只有一个节点
        children: [
            { path: '/rounterchart', component: rounterChartList, name: '轮播图管理' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'echarts管理',
        iconCls: 'el-icon-bell',
        children: [
            { path: '/echarts', component: echarts, name: '系统消息' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


