<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.content" clearable placeholder="消息内容"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getMessageList">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="messages" highlight-current-row v-loading="listLoading" style="width: 100%;" :border="true">
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
			<el-table-column prop="author" label="发布者" width="120" align="center">
			</el-table-column>
			<el-table-column prop="content" label="内容" width="350" align="center">
			</el-table-column>
			<el-table-column prop="isshow" label="是否显示" width="100" align="center" :formatter="formatIsshow">
			</el-table-column>
			<el-table-column prop="savetime" label="创建时间" width="160" sortable align="center">
			</el-table-column>
			<el-table-column prop="changetime" label="修改时间" width="160" sortable align="center">
			</el-table-column>
			<el-table-column label="操作" width="150" align="center" fixed="right">
				<template scope="scope" >
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--编辑界面-->
		<el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="120px" :rules="editFormRules" ref="editForm">
		
				<el-form-item label="发布者" prop="author">
					<el-input v-model="editForm.author"></el-input>
				</el-form-item>
				<el-form-item label="内容">
					<el-input type="textarea"  :rows="8" v-model="editForm.content"></el-input>
				</el-form-item>
				<el-form-item label="是否显示">
					<el-radio-group v-model="editForm.isshow">
						<el-radio class="radio" :label="true">是</el-radio>
						<el-radio class="radio" :label="false">否</el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getMessageAll, addMessage, editMessage, removeMessage} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
                    content: '',
                    messagetypenumber: '系统消息'
				},
				messages: [],
				listLoading: false,
				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					rounterchart: [
						{ required: true, message: '请输入消息相关内容', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
					messageid: null,
					author: '',
					content: '',
					isshow: true,
					messagetypenumber: '系统消息'
				},

			}
		},
		methods: {
			formatIsshow: function (row, column) {
				return row.isshow == 1 ? '是' : row.isshow == 0 ? '否' : '未知';
			},
			getMessageList() {
				let para = {
                    content: this.filters.content,
                    messagetypenumber: this.filters.messagetypenumber,
				};
				this.listLoading = true;
				getMessageAll(para).then((res) => {
                    console.log(res.data)
					this.messages = res.data;
					this.listLoading = false;

				});
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: row.messageid };
					removeMessage(para).then((res) => {
                            this.listLoading = false;
                            this.$message({
                                message: '删除成功',
                                type: 'success'
                            });
                            this.getMessageList();
                        });
                    }).catch(() => {
				});
			},
			//显示编辑界面
			handleEdit: function (index, row) {		
				this.editForm = Object.assign({}, row);
				this.editFormVisible = true;
			},
			//显示新增界面
			handleAdd: function () {
				this.editFormVisible = true;
				this.editForm = {
                    messageid: null,
					author: '',
					content: '',
					isshow: true,
					messagetypenumber: '系统消息'
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							let para = Object.assign({}, this.editForm);
							if(para.messageid!=null){//如果有ID
								editMessage(para).then((res) => {
									this.editLoading = false;
									this.$message({
										message: '提交成功',
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getMessageList();
								});
							}else{
								addMessage(para).then((res) => {
									this.editLoading = false;
									this.$message({
										message: '提交成功',
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getMessageList();
								});	
							}	
						});
					}
				});
			},
		},
		mounted() {
			this.getMessageList();
		}
	}
</script>

<style scoped>
</style>


